.Main {
    background-color: #6d6875;
    font-size: 1.05em; 
    grid-area: main;
    text-align: center;
    color: #f5ebe0;
    width: 100%;
}

.MainContainer {
    display: flex;
    justify-content: center;
    margin: 20px;
}

.MainSkills {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    border: 4px #946c74 solid;
    background-color: #c28486;
    margin: 10px;
    padding: 2px 20px;
}

h3 {
    font-weight: normal;
}

p {
    font-size: 1.2em;
}

li {
    text-align: left;
    padding: 4px;
    word-spacing: 2px;
}

@media screen and (max-width: 800px) {

    .MainContainer {
        flex-direction: column;
    }

}